// react-router-dom route  #5.0
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Coins from "./routes/Coins";
import Coin from "./routes/Coin";
import Chart from "./routes/Chart";
import Price from "./routes/Price";

function Router({ isDark }: { isDark: boolean }) {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/:coinId" element={<Coin />}>
          <Route path="chart" element={<Chart />} />
          <Route path="price" element={<Price />} />
        </Route>
        <Route path="/" element={<Coins isDark={isDark} />} />
      </Routes>
    </BrowserRouter>
  );
}
/*
10. path="/:coinId"  url coinId parameter   , Component useParams parameter   .  #5.0
Route Route  nested route . /:coinId/chart  .  #5.7 'horrorkist' 
*/
export default Router;
