import { useEffect, useState } from "react";
import { useQuery } from "react-query";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { fetchCoins } from "../api";

const Container = styled.div`
  padding: 0 20px;
  margin: 0 auto;
  max-width: 480px;
`;
const Header = styled.header`
  height: 15vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;
const CoinsList = styled.ul``;
const Coin = styled.li`
  background-color: ${(props) => props.theme.boxColor};
  color: ${(props) => props.theme.textColor};
  border-radius: 15px;
  margin-bottom: 10px;
  a {
    display: flex;
    align-items: center;
    padding: 20px;
    transition: color 0.2s ease-in;
  }
  &:hover {
    a {
      color: ${(props) => props.theme.accentColor};
    }
  }
`;
const Title = styled.h1`
  font-size: 48px;
  color: ${(props) => props.theme.accentColor};
`;
const Loader = styled.span`
  text-align: center;
  display: block;
`;
const Img = styled.img`
  width: 25px;
  height: 25px;
  margin-right: 10px;
`;

interface ICoin {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  is_new: boolean;
  is_active: boolean;
  type: string;
}
function Coins({ isDark }: { isDark: boolean }) {
  const { isLoading, data: coins } = useQuery<ICoin[]>("allcoins", fetchCoins);
  /*
  react-query useQuery('data Id', fetch function) fetch function   isLoading data .  #5.9
  , react-query     .  #5.9
  */
  /* 
  useQuery  coins fetch  
  const [coins, setCoins] = useState<ICoin[]>([]); // <ICoin[]>  state type   #5.3
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchCoins() {
      const response = await (await fetch("https://api.coinpaprika.com/v1/coins")).json();
      setCoins(response.slice(0, 100));
      setLoading(false);
    }
    fetchCoins();
  }, []);
  // useEffect  api data   #5.3
  */
  console.log("Coins.tsx 71", isLoading);
  console.log("Coins.tsx 71", coins);

  return (
    <Container>
      <Header>
        <Title></Title>
      </Header>
      {isLoading ? (
        <Loader>Loading...</Loader>
      ) : (
        <CoinsList>
          {coins?.slice(0, 100).map((coin) => (
            <Coin key={coin.id}>
              <Link to={`/${coin.id}`} state={{ name: coin.name }}>
                <Img src={`https://static.coinpaprika.com/coin/${coin.id}/logo.png`} alt="  " />
                {coin.name} &rarr;
              </Link>
            </Coin>
          ))}
        </CoinsList>
      )}
    </Container>
  );
}
/*
JSX      #5.3
JSX    map  #5.2
map item key .  #5.2
<Link>    a   #5.2
state={{ key:value }} react-router-dom Link state    . 
useLocation()  state   .  #5.4
*/

export default Coins;
